.ProductData{
    width: 100%;
}
.ImageCollection{
    box-sizing: border-box;

}
.ProductImage{
    width:100%;
    max-width: 80px;
    cursor: pointer;
    border: 2px solid transparent;
    border-radius: 5px;
    margin-left: 12px;

}
.SelectedProductImage{
    border: 2px solid black;
}

.FeaturesItem{
    min-height: 30px;
    min-width:100px ;
    box-sizing: content-box;
    border-radius: 10px;
    background-color: aqua;
    border: none;
    margin-right: 20px;
    cursor: pointer;
    margin-bottom: 25px;
    font-weight: 600;

}
.SelectedFeaturesItem{
    background-color: grey;
    color :white;
}
.Submit{
    color: whitesmoke;
    min-height: 45px;
    min-width:100px ;
    box-sizing: content-box;
    border-radius: 10px;
    background-color: rgb(245, 0, 0);
    margin-right: 20px;
    border: none;
    cursor: pointer;
    margin-bottom: 30px;
    font-weight: 600;
}